<template>
  <div id="TextsDetails">
    <el-container>
      <el-header height="30px">
        <div class="command">
          <div>{{ currentData }}</div>
          <div class="btns">
            <span class="add"
              ><add-pop @changeResult="getPage($event)"></add-pop
            ></span>
            <span class="upload"
              ><one-load @changePageData="getPage($event)"></one-load
            ></span>
            <span class="dowmload"><down-load></down-load></span>
            <span class="delete"><ComDelete></ComDelete></span>
          </div>
        </div>
      </el-header>
      <el-container>
        <el-aside width="180px">
          <el-col :span="24">
            <div class="right">
              <result-list></result-list>
            </div>
          </el-col>
        </el-aside>
        <el-container>
          <el-main>
            <el-table
              :data="tableData"
              :header-cell-style="{ background: 'Lavender' }"
            >
              <!-- <el-table-column type="index"> 
                    <template slot-scope="scope">
                    <span>{{ (currentPage - 1) * pageSize + scope.$index + 1 }}</span>
                    </template>
                </el-table-column> -->
              <el-table-column
                v-for="(item, index) in tableTag"
                :key="index"
                :label="item.label"
                :prop="item.dataName"
              ></el-table-column>
            </el-table>
          </el-main>
          <el-footer height="30px">
            <div class="footer">
              <el-pagination
                background
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="pageSizes"
                :page-size="pageSize"
                :layout="layout"
                :total="total"
              >
              </el-pagination>
            </div>
          </el-footer>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>
<script>
import AddPop from "../DetailsComponent/AddPop.vue";
import OneLoad from "../DetailsComponent/OneLoad.vue";
import DownLoad from "../DetailsComponent/DownLoad.vue";
import ComDelete from "../DetailsComponent/ComDelete.vue";
import ResultList from "../DetailsComponent/ResultList";
export default {
  components: {
    AddPop,
    OneLoad,
    DownLoad,
    ComDelete,
    ResultList,
  },
  data() {
    return {
      //表格数据
      tableData: [],

      tableTag: [],
      //当前页数
      currentPage: 1,
      //总条数
      // total: 400,
      total: null,
      //当前每页条数
      pageSize: 9,
      //控制每页有多少条数
      pageSizes: [9, 12],
      //控制 分页组件的布局顺序
      layout: "total, sizes, prev, pager, next, jumper",
      // 当前数据集名称
      currentData: "",
      loading: "",
    };
  },
  computed: {
    // 选中一级类型
    currentType() {
      return this.$store.state.ItemDetails.currentType;
    },
    // 二级导航栏的选中值
    itemDate() {
      return this.$store.state.ItemDetails.itemDate;
    },
    // 二级导航栏信息
    menulist() {
      return this.$store.state.ItemDetails.menulist;
    },
  },
  watch: {
    // 监听二级导航栏的选中值
    itemDate: {
      handler(newValue, oldValue) {
        if (this.currentType == "text") {
          console.log(newValue, oldValue);
          this.currentData = newValue;
          // console.log(this.currentData);
          // this.getPageData();
          this.tableData = [];
          this.tableTag = [];
          this.getPage();
        }
      },
    },
  },
  methods: {
    // 分页接口
    getPage() {
      // const that = this;
      // console.log(this.currentData);
      if (this.$store.state.ItemDetails.currentType === "text") {
        this.loading = this.$loading({
          lock: true,
          text: "正在加载图片...",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)",
        });
        this.$api
          .getPageData({
            dataSetName: this.currentData,
            page_number: this.currentPage,
            page_size: this.pageSize,
          })
          .then((res) => {
            console.log(res);
            if (res.code === 200) {
              this.tableData = res.table_data.rows_data;
              this.tableTag = res.table_header;
            }
            this.loading.close();
          });
      }
    },
    handleSizeChange(size) {
      console.log(`每页 ${size} 条`);
      this.pageSize = size;
      this.getPage();
    },
    handleCurrentChange(page) {
      console.log(`当前页: ${page}`);
      this.currentPage = page;
      this.getPage();
    },
  },
  mounted() {
    // this.reLoad();
  },
};
</script>
<style lang="scss" scoped>
#TextsDetails {
  .el-header {
    padding: 0;
    .command {
      display: flex;
      height: 30px;
      justify-content: space-between;
      align-items: center;
      color: #fff;
      padding: 0 2vh;
      background-color: #222337;
    }
    .btns {
      display: flex;
      height: 30px;
      // background-color: #f5f4f8;
      justify-content: right;
      padding-right: 0.5rem;
      span {
        margin-right: 0.25rem;
      }
    }
  }
  .el-aside {
    // min-height: 860px;
    // max-height: 860px;
    overflow: hidden;
    height: calc(100vh - 132px);
  }

  .el-main {
    padding: 0;
    margin: 0;
    height: calc(100vh - 172px);
    // min-height: 745px;
    // max-height: 745px;
    // overflow: auto;
    // margin-top: 10px;
    .el-table {
      padding: 0;
      margin: 0;
    }
  }
  .el-footer {
    padding: 0;
    margin-bottom: 10px;
    .footer {
      display: flex;
      justify-content: center;
      align-items: center;
      // height: 30px;
      background-color: #222337;
    }
  }
}
</style>
